export const query = graphql`
  query($pathSlug: String!) {
    markdownRemark(frontmatter: { path: { eq: $pathSlug } }) {
      html
      frontmatter {
        date
        title
        tags
        features{
          id
          justifyCont
          background
          feature
          featureHeader
          featureHeadCol
          featureCol
          borderCol
          videoPath{
            publicURL
          }
        }
        ffddg
        getST
        getSTSesc
        download
        docs
        docsDesc
        resourss
        resourcesArr{
        id
        resourceName
        resourceLink
        }
        downImg{
          childImageSharp {            
            fluid(              
              quality: 50 
              maxWidth: 200                        
            ) {
              ...GatsbyImageSharpFluid_withWebp
            }            
          }
        }
        ResImg {
          childImageSharp {            
            fluid(              
              quality: 50 
              maxWidth: 200                        
            ) {
              ...GatsbyImageSharpFluid_withWebp
            }            
          }
        }
        DocsImg {
          childImageSharp {            
            fluid(              
              quality: 50 
              maxWidth: 200                        
            ) {
              ...GatsbyImageSharpFluid_withWebp
            }            
          }
        }
        main_text
        main_image {
          childImageSharp {
            
            fluid(              
              quality: 80 
              maxWidth: 800                        
            ) {
              ...GatsbyImageSharpFluid_withWebp
            }            
          }
        }
        cover {
          childImageSharp {
            fluid(              
              quality: 10     
              maxWidth: 900         
            ) {
              ...GatsbyImageSharpFluid_withWebp
            }            
          }
        }
        youtube_link {
          childImageSharp {
            fluid(              
              quality: 100     
              maxWidth: 100         
            ) {
              ...GatsbyImageSharpFluid_withWebp
            }            
          }
        }
        itch_link {
          childImageSharp {
            fluid(              
              quality: 100     
              maxWidth: 100         
            ) {
              ...GatsbyImageSharpFluid_withWebp
            }            
          }
        }
        unitystore_link {
          childImageSharp {
            fluid(              
              quality: 100     
              maxWidth: 100         
            ) {
              ...GatsbyImageSharpFluid_withWebp
            }            
          }
        }
        docs_link {
          childImageSharp {
            fluid(              
              quality: 100     
              maxWidth: 200         
            ) {
              ...GatsbyImageSharpFluid_withWebp
            }            
          }
        }

      }
    }
  }
`;